@font-face {
  font-family: "Compose";
  font-weight: 300;
  font-display: swap;
  src: url("../fonts/Compose-Light.ttf") format("ttf"), url("../fonts/Compose-Light.woff") format("woff"), url("../../fonts/Compose-Light.woff2") format("woff2");
}
@font-face {
  font-family: "Compose";
  font-weight: 400;
  font-display: swap;
  src: url("../fonts/Compose-Regular.ttf") format("ttf"), url("../fonts/Compose-Regular.woff") format("woff"), url("../../fonts/Compose-Regular.woff2") format("woff2");
}
@font-face {
  font-family: "Compose";
  font-weight: 500;
  font-display: swap;
  src: url("../fonts/Compose-Medium.ttf") format("ttf"), url("../fonts/Compose-Medium.woff") format("woff"), url("../../fonts/Compose-Medium.woff2") format("woff2");
}
@font-face {
  font-family: "Compose";
  font-weight: 600;
  font-display: swap;
  src: url("../fonts/Compose-SemiBold.ttf") format("ttf"), url("../fonts/Compose-SemiBold.woff") format("woff"), url("../../fonts/Compose-SemiBold.woff2") format("woff2");
}
@font-face {
  font-family: "Compose";
  font-weight: 700;
  font-display: swap;
  src: url("../fonts/Compose-Bold.ttf") format("ttf"), url("../fonts/Compose-Bold.woff") format("woff"), url("../../fonts/Compose-Bold.woff2") format("woff2");
}
:root {
  --white-color: #fff;
  --blue-color: #00114f;
  --main-font: "Compose", sans-serif;
  --roboto: "Roboto", sans-serif;
}

*,
p,
h1,
h2,
h3 {
  padding: 0;
  margin: 0;
}

body {
  font-family: var(--main-font);
  color: var(--blue-color);
  min-width: 320px;
}
body.lock {
  overflow: hidden;
}

* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

body {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
  min-height: 100vh;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.container {
  width: 100%;
  max-width: 1500px;
  min-width: 320px;
  margin: 0 auto;
  padding: 0 63px;
}
@media (max-width: 768px) {
  .container {
    padding: 0 28px;
  }
}

.header.white,
.header.white .menu__link,
.header.white .auth__login,
.header.white .auth__signup {
  color: var(--white-color);
}

.header.dark,
.header.dark .menu__link,
.header.dark .auth__login,
.header.dark .auth__signup {
  color: var(--blue-color);
}

.header {
  position: absolute;
  top: 63px;
  left: 0;
  width: 100%;
}

.header__container {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
}

.header__navbar {
  display: flex;
  align-items: center;
}

.header__logo {
  margin-right: 38px;
}

.logo {
  position: relative;
  display: flex;
  align-items: center;
}

.logo__main {
  margin-right: 18px;
  font-weight: 700;
  font-size: 35px;
  line-height: 44px;
}

.logo__descr {
  font-weight: 500;
  font-size: 10px;
  line-height: 100%;
  letter-spacing: -0.01em;
}

.logo__link {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 3;
}

.header__line {
  margin-right: 51px;
  width: 1px;
  height: 59px;
  background-color: var(--white-color);
}

.header__menu {
  margin-right: 115px;
}

.menu__list {
  min-width: 396px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.menu__item {
  list-style-type: none;
}

.menu__link {
  font-weight: 400;
  font-size: 21px;
  line-height: 26px;
  letter-spacing: -0.01em;
  color: var(--white-color);
  text-decoration: none;
}

.header__lang {
  margin-right: 160px;
  font-weight: 700;
  font-size: 19px;
  line-height: 24px;
  letter-spacing: -0.01em;
}

.auth {
  display: flex;
  align-items: flex-end;
}

.auth__login,
.auth__signup {
  font-weight: 700;
  font-size: 19px;
  line-height: 24px;
  letter-spacing: -0.01em;
  color: var(--white-color);
  text-decoration: none;
}

.auth__login {
  margin-right: 69px;
  margin-bottom: 18px;
}

.auth__signup {
  display: inline-block;
  width: 188px;
  height: 68px;
  margin-top: 9px;
  padding: 26px 33px 18px 44px;
  border: 1px solid rgba(255, 255, 255, 0.6);
}

.main {
  flex-grow: 1;
}

.intro {
  width: 100%;
  height: 100vh;
  min-width: 500px;
}

.intro__slider,
.intro__wrapper {
  width: 100%;
  height: 100%;
}

.intro__item {
  width: 100%;
  height: 100%;
}

.intro__bg {
  width: 100%;
  height: 100%;
}
.intro__bg img {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

footer {
  padding: 20px;
}